import { useMemo } from 'react';
import {
  useAppConfigContext,
  usePatternsWizardOverridesContext,
} from '../providers';
import { useModalContext } from '../providers/ModalContext';
import { useNavigation } from './useNavigationUtils';
import { BaseSDK } from '../types';

export const useBaseSDK = () => {
  const { openModal, closeModal } = useModalContext();
  const appConfig = useAppConfigContext();
  const overrides = usePatternsWizardOverridesContext();
  const { navigateToEntityPage } = useNavigation();

  /*  eslint-disable react-hooks/exhaustive-deps */
  // disabled because bug in Patterns that navigateToEntityPage isn't wrapped with useCallback,
  const baseSDK = useMemo<BaseSDK>(
    () => ({
      appConfig,
      overrides,
      openModal,
      closeModal,
      navigateToEntityPage,
    }),
    [appConfig, overrides, openModal, closeModal, navigateToEntityPage],
  );

  /*  eslint-enable react-hooks/exhaustive-deps */

  return baseSDK;
};
